<template>
  <div class="order_list">
    <van-tabs
      sticky
      :active="activeIndex"
      :swipe-threshold="5"
      @click="onClick">
      <van-tab
        v-for="tab in tabsItem"
        :title="tab.name"
        :key="tab.type"
        :status="tab.status"></van-tab>
    </van-tabs>
    <van-list
      :immediate-check="false"
      :offset="100">
      <van-panel
        v-for="(order, i) in orders"
        :key="i"
        :title="': ' + order.order_number"
        :status="getStatusText(order.state)">
        <div>
          <van-card
            v-for="(goods, goodsIndex) in order.orderItems"
            class="order_list--van-card"
            :key="goodsIndex"
            :title="goods.item_name"
            :num="goods.num"
            :price="goods.price"
            :thumb="goods.cover"
            @click.native="toOrderDetail(i)"/>
            <div class="order_list--total">
              : {{ order.total_fee }}{{ order.ship_fee }}
            </div>
        </div>
        <component
          slot="footer"
          :is="'status' + order.state"
          :reminder="order.is_can_reminder"
          @handle="actionHandle($event, i)"/>
      </van-panel>
    </van-list>
  </div>
</template>

<script>
import { Tab, Tabs, Panel, Card, List } from 'vant';
import status1 from './footers/status1';
import status2 from './footers/status2';
import status3 from './footers/status3';
import status4 from './footers/status4';
import status8 from './footers/status8';

const STATUS_TEXT = {
  1: '',
  2: '',
  3: '',
  4: '',
  5: '',
  6: '',
  7: '',
  8: '',
};

export default {
  components: {
    [Tab.name]: Tab,
    [Tabs.name]: Tabs,
    [Panel.name]: Panel,
    [Card.name]: Card,
    [List.name]: List,
    [status1.name]: status1,
    [status2.name]: status2,
    [status3.name]: status3,
    [status4.name]: status4,
    [status8.name]: status8,
  },

  props: {
    active: {
      type: [String, Number],
      default: 0,
    },
  },

  data() {
    const activeIndex = this.active;
    return {
      shop_id: 1,
      activeIndex,
      orders: [],
      tabsItem: [
        {
          name: '',
          status: 0,
        },
        {
          name: '',
          status: 1,
        },
        {
          name: '',
          status: 2,
        },
        {
          name: '',
          status: 3,
        },
        {
          name: '',
          status: 4,
        },
      ],
    };
  },

  created() {
    // eslint-disable-next-line
    console.log(this.active);
    // 
    this.initOrders();
  },

  methods: {
    initOrders() {
      // 
      this.$ajax
        .get('/api/orders')
        .then((response) => {
          // eslint-disable-next-line
          console.log(response.data.items);
          this.orders = this.assembleOrders(response.data.items);
        });
    },
    assembleOrders(items) {
      return items.map(item => ({
        id: item.id,
        order_number: item.order_number,
        total_fee: item.total_fee,
        ship_fee: item.ship_fee,
        is_can_reminder: item.state === 2,
        state: item.state,
        orderItems: item.products.map(product => ({
          item_name: product.title,
          num: product.num,
          price: product.price,
          cover: product.cover,
        })),
      }));
    },
    onClick(index, title) {
      /* eslint-disable */
      console.log(index);
      this.$toast(title);
      // 
      this.changeItem(index);
    },
    toOrderShow(index) {
      this.$router.push({name: 'OrderShow', params: { id: index }});
    },
    toPay(index) {
      /* eslint-disable */
      this.$dialog.confirm({
        title: '',
        message: ''
      }).then(() => {
        console.log('Yes');
      }).catch(() => {
        console.log('No');
      });
      console.log(index);
    },
    cancelOrder(i) {
      this.$dialog.confirm({
        title: '',
        message: ''
      }).then(() => {
        console.log('Yes');
        console.log(i);
        console.log(this.orders[i]);
        // 
        this.$ajax
          .put('/api/orders/' + this.orders[i].id + '/cancel')
          .then((response) => {
            if (response.data.code === 200) {
              this.$toast('');
              this.orders[i].state = 8;
            } else {
              this.$toast('');
            }
          });
      }).catch((error) => {
        console.log(error);
        console.log('No');
        this.$toast('');
      });
    },
    receiptOrder(i) {
      this.$dialog.confirm({
        title: '',
        message: ''
      }).then(() => {
        console.log('Yes');
        console.log(i);
        console.log(this.orders[i]);
        // 
        this.$ajax
          .put('/api/orders/' + this.orders[i].id + '/receipt')
          .then((response) => {
            if (response.data.code === 200) {
              this.$toast('');
              this.orders[i].state = 4;
            } else {
              this.$toast('');
            }
          });
      }).catch((error) => {
        console.log(error);
        console.log('No');
        this.$toast('');
      });
    },
    reminderOrder(i) {
      this.orders[i].is_can_reminder = false;
      this.$toast(', ~');
    },
    actionHandle(handle, i) {
      /* eslint-disable */
      console.log(handle);
      this[handle] && this[handle](i);
    },
    changeItem(state) {
      // 
      this.$ajax
        .get('/api/orders?state=' + state)
        .then((response) => {
          // eslint-disable-next-line
          this.orders = this.assembleOrders(response.data.items);
        });
    },
    getStatusText(state) {
      return STATUS_TEXT[state];
    },
  },
};
</script>

<style lang="less" scoped>
.order_list {
  padding-bottom: 0;
  &--footer_btn {
    text-align: right;
  }
  &--panel {
    margin-bottom: 10px;
  }

  &--van-card {
    background-color: #fafafa;
  }

  &--total {
    text-align: right;
    padding: 10px;
  }
}
</style>



